
@font-face {
  font-family: "\5B8B\4F53";
  src: url(../../static/fonts/simsun.31d9eb6.ttc);
}
@font-face {
  font-family: "\7E41\4F53";
  src: url(../../static/fonts/FZQTFW.d46bc42.TTF);
}
#app {
  font-family: "\5B8B\4F53";
  width: 100vw;
  height: 100vh;
  margin: 0;
  padding: 0;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    /* padding-right: 20px; */
  /* overflow-x: hidden; */
  /* overflow-y: scroll; */
}
#app::-webkit-scrollbar {
  width: 0 !important;
  height: 0;
   display: none;
}
html::-webkit-scrollbar {
  width: 0 !important;
   display: none;
}
.routerview{
  padding-right: -20px;
  overflow-x: hidden;
}
*:focus {
  outline: none;
}
body {
  margin: 0;
  padding: 0;
}
html {
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
  color: black;
}
.routerview {
  overflow: scroll;
  scroll-behavior: initial;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}

.headbar {
  width: 100%;
  height: 8vh;
  /* background-color: aqua; */
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  background-color: white;
  z-index: 100;
}
.selection,
.selection2,
.selection3 {
  width: 10vw;
  line-height: 10vh;
  margin-left: 3vw;
  text-align: center;
  margin-bottom: -1px;
  color: rgba(0, 0, 0, 0.6);
  -webkit-transition: 0.5s all ease;
  transition: 0.5s all ease;
  /* border-bottom: 2px solid rgba(0, 0, 0, 0.1); */
}
.selection2,
.selection3 {
  margin-left: 10vw;
}
.selection3 {
  width: 20vw;
}
.selected {
  border-bottom: 2px solid rgb(0, 106, 82);
  color: black;
}

