/* 侧边操作栏 */
.operation {
  position: fixed;
  width: 40px;
  height: 60vh;
  top: 40vh;
  /* background-color: blueviolet; */
}
.study-selection {
  margin: 0;
  width: 40px;
  height: 15vh;
  -webkit-writing-mode: vertical-lr;
      -ms-writing-mode: tb-lr;
          writing-mode: vertical-lr;
  line-height: 20px;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.3);
  letter-spacing: 3px;
  text-align: center;
  -webkit-transition: 0.5s ease;
  transition: 0.5s ease;
  /* background-color: blue; */
}
.selectThis {
  border-left: 1vw solid rgb(0, 106, 82);
  color: rgb(0, 106, 82);
  font-weight: bold;
}

/* 主体内容 */
.container {
  margin: 5vh;
  margin-top: 10vh;
  overflow: scroll;
  background-color: rgba(0, 0, 0, 0.02);
  background: url(../../static/img/pattern4.898624b.png) repeat;
  background-size: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
}

/* 隐藏滚动条 */
.container::-webkit-scrollbar {
  width: 0 !important;
  display: none;
}
body::-webkit-scrollbar {
  width: 0 !important;
  display: none;
}
.headimg img {
  width: 100%;
